import None from '../../public/noNumber'
import Loading from '../../public/Loading'
import Detail from "../../public/detail.vue"
export default {
  data() {
    return {
      api:this.GLOBAL.baseURL,
      outdoorList:[],
      isLoading:false,
      loadingState:false,
      page:1,
      request:false,
    };
  },
  components:{
    Detail,
    Loading,
    None
  },
  methods:{
    getStory(val){
      this.$http.post(this.api + '/home/Story/story_list',{
        token: localStorage.getItem('token'),
        kind_id:3,
        sort:2,
        page:val,
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data.data
            for(let i = 0;i<data.length;i++){
              let user = data[i].user
              let avator = ''
              let zan = ''
              if(data[i].cover_image){
                if(user.head_image){
                  avator = user.headimage.domain + user.headimage.image_url
                }else{
                  avator =  '../../static/img/static/user.png'
                }
                if(data[i].is_praise){
                  zan = 1
                }else{
                  zan = 0
                }
                this.outdoorList.push({
                  img:data[i].cover.domain + data[i].cover.image_url,
                  address:data[i].province,
                  remark: data[i].title,
                  avator:avator,
                  zan: data[i].praise_num,
                  say: data[i].leaving_num,
                  zanIndex:zan,
                  story_id: data[i].story_id,
                  userId: data[i].user_id
                })
              }
            }
            if(!data.length){
              this.request = true
            }else{
              this.request = false
            }
            this.isLoading = false
            this.loadingState = false

          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/Index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.getStory(val)
              })
          }else{
            alert(res.data.msg)
          }
        })
    },
    toperson(item,index){
      this.$router.push({
        path: '/userdata',
        query: {
          information: item.userId
        }
      })
    },
    toStory(item,index){
      this.$router.push({
        path: '/storyPage',
        query:{
          information: item.story_id
        }
      })
    },
    //点赞
    addZan(item,index){
      if(item.zanIndex == 0){
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 1,
          table_id: item.story_id,
          type: 1
        })
          .then(res=>{
            if(res.data.code == 1){
              item.zanIndex = 1
              item.zan = item.zan + 1
            }else if(res.data.code == 3){
             this.$http.post(this.api + '/home/index/token')
               .then(res=>{
                 localStorage.setItem('token',res.data.data)
                 this.addZan(item,index)
               })
            }else{
              alert(res.data.msg)
            }
          })

      }else{
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 1,
          table_id: item.story_id,
          type: 2
        })
          .then(res=>{
            if(res.data.code == 1){
              console.log(res)
              item.zan = item.zan - 1
              item.zanIndex = 0
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.addZan(item,index)
                })
            }else{
              alert(res.data.msg)
            }
          })
      }
    },
    onScroll(){
      var scrollTop = document.documentElement.scrollTop||document.body.scrollTop;
      //变量windowHeight是可视区的高度
      var windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
      //变量scrollHeight是滚动条的总高度
      var scrollHeight = document.documentElement.scrollHeight||document.body.scrollHeight;
      //滚动条到底部的条件
      if(scrollHeight == scrollTop+windowHeight){
        //写后台加载数据的函数
        if(!this.request){
          this.loadingState = true
          this.page += 1
          this.getStory(this.page)
        }
      }
    }
  },
  mounted(){
    window.scroll(0,0)
    this.getStory(this.page)
    window.addEventListener('scroll',this.onScroll)
  },
  destroyed(){
    window.removeEventListener('scroll',this.onScroll)
  }
}



